.header {
    height: 60px;
    display:flex;
    align-items:center;
    background-color:#131921;
    position:sticky;
    top:0;
    z-index:100;
}

.product {
    display: flex;
    flex-direction: row;
}

.header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
}


.header__search{
    display:flex;
    flex:1;
    align-items:center;
    border-radius:24px;
}

.header__nav{
    display:flex;
    justify-content:space-evenly;
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}

.header__option{
    display:flex;
    flex-direction:column;
    margin-left:10px;
    margin-right:10px;
    color:white;
}

.header__basketCount{
    margin-left:10px;
    margin-right:10px;
}

.header__optionOne {
    font-size: 10px;
}

.header__optionTwo{
    font-size:13px;
    font-weight:800;
}

.header__optionBasket{
    display:flex;
    align-items:center;
    color:white;
}